import React from 'react';
import SketchPicker from './sketch/index.js';

type IColorPickerSketchProps = {
  color: string;
  onChange?: (color: string) => void;
  onChangeComplete?: (color: string) => void;
}

const toString = (color: Parameters<Exclude<React.ComponentProps<typeof SketchPicker>['onChange'], undefined>>[0]): string => {

  if (typeof color.rgb.a === 'number' && color.rgb.a < 1) {
    const { r, g, b, a } = color.rgb;
    return `rgba(${r}, ${g}, ${b}, ${a})`;
  } else {
    return color.hex;
  }
}


export const ColorPickerSketch = React.memo(
  (props: IColorPickerSketchProps) => {

    const {
      color,
      onChange,
      onChangeComplete,
    } = props;

    return React.createElement(
      SketchPicker,
      {
        color,
        onChange: onChange ? (e => onChange(toString(e))) : undefined,
        onChangeComplete: onChangeComplete ? (e => onChangeComplete(toString(e))) : undefined
      }
    );
  }
)
